<template>
    <div class="body">

        <el-steps :active="active" finish-status="success" space="1000px">
            <el-step title="步骤 1"></el-step>
            <el-step title="步骤 2"></el-step>
            <el-step title="步骤 3"></el-step>
        </el-steps>




        <el-badge :value="value1" class="item">
            <el-button size="small" v-on:click="showPic()">最美风景图</el-button>
        </el-badge><br><br>
        <div v-show="show">
            <img src="@/assets/01.jpg" alt="" width="420px" height="300px">

            <!-- <img src="@/assets/02.jpg" alt="" width="420px" height="300px"> -->
        </div>
        <el-badge :value="value2" class="item">
            <el-button size="small" v-on:click="showWord()">你有一条未读消息</el-button>
        </el-badge><br>
        <br>

        <div v-show="showWords">
            <img src="@/assets/photo.png" alt="" width="150px" height="50">
            <span>时间过得好快啊，一年又一年。小时候你欺负我的事情就不计较了。在此愿你的每一天彩色斑斓，愿你永远青春。愿你早点找到一个男朋友，但是也别把我忘记了（呜呜）</span>
        </div><br><br>
        <!-- <el-divider></el-divider> -->
        <el-badge :value="value3" class="item">
            <el-button size="small" v-on:click="showNotice()">注意事项</el-button>
        </el-badge>
        <div v-show="showNo">此内容不适合发朋友圈，作者的脸皮很薄，切记。再三说明！！！！</div>


    </div>
</template>
  
<script>
export default {
    data() {
        return {
            value1: 1,
            value2: 1,
            value3: 1,
            active: 2,
            show: false,
            showWords: false,
            showNo: false,
        }
    },
    methods: {
        showPic() {
            if (this.value1 > 0)
                this.value1--;
            this.show = true;
        },
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        showWord() {
            if (this.value2 > 0)
                this.value2--;
            this.showWords = true;
        },
        showNotice() {
            if (this.value3 > 0)
                this.value3--;
            this.showNo = true;
        },
        createSakura() {
            const sakura = document.createElement('div');
            sakura.className = 'sakura';

            const left = Math.random() * window.innerWidth;
            const animationDuration = Math.random() * 6 + 3;

            sakura.style.left = `${left}px`;
            sakura.style.animationDuration = `${animationDuration}s`;

            sakura.innerHTML = '&#x1F338;';

            document.body.appendChild(sakura);

            sakura.addEventListener('animationend', () => {
                sakura.parentNode.removeChild(sakura);
            });
        }


    },
    mounted() {
        this.$notify({
            title: '祝贺',
            message: '恭喜完成步骤二',
            duration: 10000

        });

        this.timer = setInterval(() => {
        this.createSakura();
      }, 1000);
        // for (let i = 0; i < 10; i++) {
        //     this.$notify({
        //         title: '警告',
        //         message: '你的系统出现了故障',
        //         duration: 10000 - 1000 * i,
        //         offset: 45 * i
        //     });
        // }

    }

}
</script>
  
<style>
.body {
    background-color: #f0f0f0;
    overflow: hidden;
}

.sakura {
    position: absolute;
    top: -10px;
    z-index: 1;
    pointer-events: none;
    animation: fall linear infinite;
}

@keyframes fall {
    0% {
        transform: translateY(-10%);
    }

    100% {
        transform: translateY(110vh);
    }
}
</style>
  